import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import styled from "styled-components"

import Header from "../components/index/header"
import Explore from "../components/index/explore"
import Button from "../components/index/button"
import Container from "../components/stylesComponents/container"
import CircleLoader from "react-spinners/CircleLoader"
import "../components/index.css"

const LoadingContainer = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`

class IndexPage extends React.Component {
  state = {
    isLoading: true,
  }

  componentDidMount() {
    // fixing 100vh on mobile
    const vh = window.innerHeight * 0.01
    document.documentElement.style.setProperty("--vh", `${vh}px`)
    this.setState({
      isLoading: false,
    })
  }

  render() {
    if (this.state.isLoading === true) {
      return (
        <LoadingContainer>
          <CircleLoader sizeUnit={"px"} size={100} />
        </LoadingContainer>
      )
    }

    return (
      <Layout>
        <SEO
          title="Explore"
          keywords={[`developer`, `CV`, `Oslo`, "Michal", "Konczak"]}
        />
        <Container>
          <Header />
          <Button />
          <Explore />
        </Container>
      </Layout>
    )
  }
}

export default IndexPage
